import { useSelector } from "react-redux";
import { cartInfoSel } from "../../storage/cart/cartSlice";
import cn from "classnames";
import s from "./index.module.css";

const CartAmount = () => {
  const state = useSelector((state) => state);
  const { amountWithDiscount, totalDiscount, amount, totalCount } =
    cartInfoSel(state);

  return (
    <div className={s.cartAmount}>
      <h1 className={s.title}> </h1>
      <div className={s.table}>
        <div className={s.tableRow}>
          <span className={s.tableTitle}> ({totalCount})</span>
          <span className={s.tableValue}>{amount} </span>
        </div>
        {totalDiscount !== 0 && (
          <div className={s.tableRow}>
            <span className={s.tableTitle}></span>
            <span className={cn(s.tableValue, s.tableValueDiscount)}>
              - {totalDiscount} 
            </span>
          </div>
        )}
      </div>
      <div className={s.totalCost}>
        <h2 className={s.totalCostTitle}> </h2>
        <span className={s.totalCostValue}>{amountWithDiscount} </span>
      </div>
      <div className="btn btn_type_primary btn_type_wide"> </div>
    </div>
  );
};

export default CartAmount;
